}
.aainvoice .aainvoice-items td {
    border-top: #eee 1px solid;
}
.aainvoice .aainvoice-items .aatotal td {
    border-top: 2px solid #333;
    border-bottom: 2px solid #333;
    font-weight: 700;
}
